import { useComponent } from "./components";
import { useBrowser } from "./hooks";
import ContextMenu from "./components/context-menu/index";
import "./static/index.scss";

const CRUD = {
	install(app: any, options: any) {
		let { crud } = options || {};

		if (!crud) crud = {};

		// 
		if (!crud.style) crud.style = {};

		// 
		if (!crud.table) crud.table = {};

		// 
		app.provide("crudConf", crud);

		// @ts-ignore
		window.__crud_app__ = app;

		// 
		useBrowser(app);

		// 
		useComponent(app);

		// 
		app.config.globalProperties.$crud = {
			openContextMenu: ContextMenu.open
		};

		// elm
		setTimeout(() => {
			const { $message, $messageBox } = app.config.globalProperties;

			// @ts-ignore
			window.ElMessage = $message;

			// @ts-ignore
			window.ElMessageBox = $messageBox;
		}, 0);

		return {
			name: "crud"
		};
	}
};

export default CRUD;

export { ContextMenu, CRUD };
export * from "./hooks";
