import { defineComponent, inject, ref, watch } from "vue";
import { Crud } from "../types";

export default defineComponent({
	name: "cl-search-key",

	props: {
		// 
		modelValue: String,
		// 
		field: {
			type: String,
			default: "keyWord"
		},
		// 
		fieldList: {
			type: Array,
			default: () => []
		},
		// 
		onSearch: Function,
		// 
		placeholder: {
			type: String,
			default: ""
		}
	},

	emits: ["update:modelValue", "change", "field-change"],

	setup(props, { emit }) {
		const crud = inject("crud") as Crud;

		// 
		const selectField = ref<string>(props.field);

		// 
		const value = ref<string>("");

		watch(
			() => props.modelValue,
			(val: any) => {
				value.value = val;
			},
			{
				immediate: true
			}
		);

		// 
		function search() {
			const params: any = {};

			props.fieldList.forEach((e: any) => {
				params[e.value] = null;
			});

			function next(newParams?: any) {
				crud.refresh({
					page: 1,
					...params,
					[selectField.value]: value.value,
					...newParams
				});
			}

			if (props.onSearch) {
				props.onSearch(params, { next });
			} else {
				next();
			}
		}

		// 
		function onKeydown({ keyCode }: any) {
			if (keyCode === 13) {
				search();
			}
		}

		// 
		function onInput(val: string) {
			emit("update:modelValue", val);
			emit("change", val);
		}

		// 
		function onFieldChange() {
			emit("field-change", selectField.value);
			onInput("");
			value.value = "";
		}

		return {
			value,
			selectField,
			search,
			onKeydown,
			onInput,
			onFieldChange
		};
	},

	render(ctx: any) {
		return (
			<div class="cl-search-key">
				<el-select
					class="cl-search-key__select"
					filterable
					size="mini"
					v-model={ctx.selectField}
					v-show={ctx.fieldList.length > 0}
					onChange={ctx.onFieldChange}>
					{ctx.fieldList.map((e: any, i: number) => (
						<el-option key={i} label={e.label} value={e.value} />
					))}
				</el-select>

				<el-input
					class="cl-search-key__input"
					v-model={ctx.value}
					placeholder={ctx.placeholder}
					onKeydown={ctx.onKeydown}
					onInput={ctx.onInput}
					clearable
					size="mini"
				/>

				<el-button
					class="cl-search-key__button"
					type="primary"
					size="mini"
					onClick={ctx.search}>
					
				</el-button>
			</div>
		);
	}
});
