import { defineComponent, h, inject, ref } from "vue";
import type { PropType } from "vue";
import { useForm, useRefs } from "../../hooks/core";
import { Crud, UpsertItem } from "../../types";
import { provide } from "vue";
import { useFormApi } from "../form/helper";

export default defineComponent({
	name: "cl-upsert",

	props: {
		// 
		modelValue: {
			type: Object,
			default: () => {
				return {};
			}
		},
		// 
		items: {
			type: Array as PropType<UpsertItem[]>,
			default: () => []
		},
		// el-form 
		props: {
			type: Object,
			default: () => {
				return {};
			}
		},
		// 
		sync: Boolean,
		//  { hidden, saveButtonText, closeButtonText, buttons }
		op: Object,
		// cl-dialog  { props, hiddenControls, controls }
		dialog: Object,
		//  { isEdit, data, { submit, done, close } }
		onOpen: Function,
		//  { done }
		onClose: Function,
		//  { data, { next, done, close } }
		onInfo: Function,
		//  { isEdit, data, { next, done, close } }
		onSubmit: Function
	},

	emits: ["opened", "closed"],

	setup(props, { emit }) {
		const { refs, setRefs }: any = useRefs();
		const { setFormData } = useForm(props);

		// 
		const mitt = inject<any>("mitt");
		const crud = inject("crud") as Crud;

		// 
		const form: any = setFormData();
		// 
		const isEdit = ref<boolean>(false);
		// 
		const isLoaded = ref<boolean>(false);

		// 
		function close() {
			refs.value.form.close();
			isLoaded.value = false;
			emit("closed");
		}

		// 
		function beforeClose() {
			if (props.onClose) {
				props.onClose(close);
			} else {
				close();
			}
		}

		// 
		function submit(data: any, event?: any) {
			function done() {
				return event ? event.done() : refs.value.form.done();
			}

			async function next(data: any) {
				// 
				const reqName: string = crud.dict.api[isEdit.value ? "update" : "add"];

				// 
				if (!crud.service[reqName]) {
					done();
					return Promise.reject(`Request function '${reqName}' is not fount!`);
				}

				// 
				await crud.service[reqName](data)
					.then((res: any) => {
						ElMessage.success("");
						close();
						crud.refresh();
						return res;
					})
					.catch((err: string) => {
						ElMessage.error(err);
						Promise.reject(err);
					});

				// 
				done();
			}

			// 
			if (props.onSubmit) {
				props.onSubmit(isEdit.value, data, {
					done,
					next,
					close
				});
			} else {
				next(data);
			}
		}

		// 
		function open() {
			return new Promise((resolve) => {
				if (!refs.value.form) {
					return false;
				}

				refs.value.form.open({
					title: isEdit.value ? "" : "",
					props: props.props,
					items: props.items,
					op: props.op,
					dialog: props.dialog,
					on: {
						open: (_: any, { done, close }: any) => {
							if (props.onOpen) {
								props.onOpen(isEdit.value, form, {
									submit: () => {
										submit(form);
									},
									done,
									close
								});
							}

							resolve(true);
						},
						submit,
						close: beforeClose
					},
					_data: {
						isEdit: isEdit.value
					}
				});
			});
		}

		// 
		function onOpened(data: any) {
			isLoaded.value = true;
			emit("opened", isEdit.value, data);
		}

		// 
		async function add() {
			isEdit.value = false;
			await open();
			onOpened({});
		}

		// 
		async function append(data: any) {
			isEdit.value = false;
			await open();
			if (data) {
				refs.value.form.reBindForm(data);
			}
			onOpened(form);
		}

		// 
		function edit(data?: any) {
			if (!refs.value.form) {
				return false;
			}

			const { showLoading, hiddenLoading } = refs.value.form;

			// 
			isEdit.value = true;

			// 
			showLoading();

			// 
			if (!props.sync) {
				open();
			}

			// 
			const done = (data?: any) => {
				// 
				hiddenLoading();

				// 
				if (data) {
					refs.value.form.reBindForm(data);
				}

				onOpened(form);
			};

			// 
			const close = () => {
				hiddenLoading();
				close();
			};

			// 
			const next = async (data: any) => {
				// 
				const reqName: any = crud.dict.api.info;

				// 
				if (!crud.service[reqName]) {
					done();
					return Promise.reject(`Request function '${reqName}' is not fount!`);
				}

				// 
				const res = await crud.service[reqName]({
					id: data.id
				})
					.then((res: any) => {
						done(res);

						// 
						if (props.sync) {
							open();
						}

						return res;
					})
					.catch((err: string) => {
						ElMessage.error(err);
						return Promise.reject(err);
					});

				// 
				hiddenLoading();

				return res;
			};

			// 
			if (props.onInfo) {
				props.onInfo(data, {
					next,
					done: (data: any) => {
						done(data);
						emit("opened", true, form);
					},
					close
				});
			} else {
				next(data);
			}
		}

		// 
		(function () {
			mitt.on("crud.add", add);
			mitt.on("crud.append", append);
			mitt.on("crud.edit", edit);
			mitt.on("crud.close", close);
		})();

		// 
		provide("upsert", {
			isLoaded,
			isEdit,
			add,
			append,
			close,
			submit
		});

		return {
			refs,
			setRefs,
			form,
			isEdit,
			add,
			append,
			edit,
			close,
			...useFormApi({ refs })
		};
	},

	render(ctx: any) {
		return (
			<div class="cl-upsert">
				{h(
					<cl-form ref={ctx.setRefs("form")} v-model={ctx.form}></cl-form>,
					{},
					ctx.$slots
				)}
			</div>
		);
	}
});
