import { defineComponent, h, inject, nextTick, onMounted, ref } from "vue";
import type { PropType } from "vue";
import ContextMenu from "../context-menu/index";
import { useElTableApi } from "./helper";
import { cloneDeep, isArray, isEmpty, isFunction, isNull, isBoolean } from "../../utils";
import { renderNode } from "../../utils/vnode";
import { useRefs } from "../../hooks/core";
import { Crud, Mitt, Browser, TableColumn } from "../../types";

export default defineComponent({
	name: "cl-table",

	props: {
		columns: {
			type: Array as PropType<TableColumn[]>,
			required: true,
			default: () => []
		},
		on: {
			type: Object,
			default: () => {
				return {};
			}
		},
		props: {
			type: Object,
			default: () => {
				return {};
			}
		},
		height: Number,
		// 
		autoHeight: {
			type: Boolean,
			default: true
		},
		// 
		contextMenu: {
			type: [Boolean, Array],
			default: undefined
		},
		// 
		sortRefresh: {
			type: Boolean,
			default: true
		}
	},

	emits: ["selection-change", "sort-change"],

	setup(props, { emit }) {
		const browser = inject("browser") as Browser;
		const { refs, setRefs }: any = useRefs();

		// 
		const mitt = inject("mitt") as Mitt;
		const crud = inject("crud") as Crud;

		// el-table api
		const {
			clearSelection,
			toggleRowSelection,
			toggleAllSelection,
			toggleRowExpansion,
			setCurrentRow,
			clearSort,
			clearFilter,
			doLayout,
			sort
		} = useElTableApi({ refs });

		// 
		const visible = ref<boolean>(true);

		// 
		const data = ref<Array<any>>([]);

		// 
		const maxHeight = ref<number>(0);

		// 
		function changeSort(prop: string, order: string) {
			if (order === "desc") {
				order = "descending";
			}

			if (order === "asc") {
				order = "ascending";
			}

			sort(prop, order);
		}

		// 
		function onSelectionChange(selection: Array<any>) {
			crud.selection.splice(0, crud.selection.length, ...selection);
			emit("selection-change", selection);
		}

		// 
		function onSortChange({ prop, order }: any) {
			if (props.sortRefresh) {
				if (order === "descending") {
					order = "desc";
				}

				if (order === "ascending") {
					order = "asc";
				}

				if (!order) {
					prop = null;
				}

				crud.refresh({
					prop,
					order,
					page: 1
				});
			}

			emit("sort-change", { prop, order });
		}

		// 
		function onRowContextMenu(row: any, column: any, event: any) {
			// 
			const cm: any =
				isEmpty(props.contextMenu) && !isArray(props.contextMenu)
					? crud.table!["context-menu"]
					: props.contextMenu;

			// 
			let buttons = ["refresh", "check", "edit", "delete", "order-asc", "order-desc"];
			// 
			let enable = false;

			if (cm) {
				if (isArray(cm)) {
					buttons = cm || [];
					enable = Boolean(buttons.length > 0);
				} else {
					enable = true;
				}
			}

			if (enable) {
				// 
				const list = buttons
					.map((e: any) => {
						switch (e) {
							case "refresh":
								return {
									label: "",
									callback(_: any, done: Function) {
										crud.refresh();
										done();
									}
								};
							case "edit":
							case "update":
								return {
									label: "",
									hidden: !crud.getPermission("update"),
									callback(_: any, done: Function) {
										crud.rowEdit(row);
										done();
									}
								};
							case "delete":
								return {
									label: "",
									hidden: !crud.getPermission("delete"),
									callback(_: any, done: Function) {
										crud.rowDelete(row);
										done();
									}
								};
							case "check":
								return {
									label: crud.selection.find((e: any) => e.id == row.id)
										? ""
										: "",
									hidden: !props.columns.find((e: any) => e.type === "selection"),
									callback(_: any, done: Function) {
										toggleRowSelection(row);
										done();
									}
								};
							case "order-desc":
								return {
									label: `${column.label} - `,
									hidden: !column.sortable,
									callback(_: any, done: Function) {
										changeSort(column.property, "desc");
										done();
									}
								};
							case "order-asc":
								return {
									label: `${column.label} - `,
									hidden: !column.sortable,
									callback(_: any, done: Function) {
										changeSort(column.property, "asc");
										done();
									}
								};
							default:
								if (isFunction(e)) {
									return e(row, column, event);
								} else {
									return e;
								}
						}
					})
					.filter((e) => Boolean(e) && !e.hidden);

				// 
				if (list.length > 0) {
					ContextMenu.open(event, {
						list
					});
				}
			}

			// 
			if (props.props.onRowContextmenu) {
				props.props.onRowContextmenu(row, column, event);
			}
		}

		// 
		function calcMaxHeight() {
			if (!props.autoHeight) {
				return false;
			}

			nextTick(() => {
				let vm: any = refs.value.table;
				let h = 15;

				// 
				while (!vm.$parent?.$el.className.includes("cl-crud")) {
					vm = vm.$parent;
				}

				// 
				h += vm.$el.offsetTop;

				// 
				let n = vm.$el.nextSibling;

				while (n && (n.className || "").includes("el-row")) {
					h += n.clientHeight + 5;
					n = n.nextSibling;
				}

				// 
				maxHeight.value = crud.crudRef.clientHeight - h;
			});
		}

		// 
		function showColumn(prop: string | string[], status?: boolean) {
			const keys = isArray(prop) ? prop : [prop];
			visible.value = false;

			props.columns
				.filter((e) => (e.prop ? keys.includes(e.prop) : false))
				.forEach((e) => {
					e.hidden = isBoolean(status) ? status : false;
				});

			nextTick(() => {
				visible.value = true;
			});
		}

		// 
		function hiddenColumn(prop: string | string[]) {
			showColumn(prop, true);
		}

		// 
		(function () {
			// 
			mitt.on("crud.refresh", ({ list }: any) => {
				data.value = list;
			});

			// 
			mitt.on("crud.resize", () => {
				calcMaxHeight();
			});
		})();

		// 
		(function () {
			const { order, prop } = props.props["default-sort"] || {};

			if (order && prop) {
				crud.params.order = order === "descending" ? "desc" : "asc";
				crud.params.prop = prop;
			}
		})();

		onMounted(function () {
			calcMaxHeight();
		});

		return {
			browser,
			refs,
			visible,
			data,
			maxHeight,
			setRefs,
			showColumn,
			hiddenColumn,
			onSelectionChange,
			onSortChange,
			onRowContextMenu,
			clearSelection,
			toggleRowSelection,
			toggleAllSelection,
			toggleRowExpansion,
			setCurrentRow,
			clearSort,
			clearFilter,
			doLayout,
			sort
		};
	},

	render(ctx: any) {
		const crud = inject("crud") as Crud;

		// 
		const renderColumn = () => {
			return ctx.columns
				.filter((e: any) => !e.hidden)
				.map((item: any, index: number) => {
					const ElTableColumn = (
						<el-table-column
							key={`crud-table-column-${index}`}
							align="center"></el-table-column>
					);

					// 
					if (item.type === "op") {
						return h(
							ElTableColumn,
							{
								label: "",
								width: "160px",
								fixed: ctx.browser.isMini ? null : "right",
								...item
							},
							{
								default: (scope: any) => {
									return (
										<div class="cl-table__op">
											{(item.buttons || ["edit", "delete"]).map(
												(vnode: any) => {
													if (vnode === "edit") {
														return (
															<el-button
																size="mini"
																type="text"
																v-show={crud.getPermission(
																	"update"
																)}
																onClick={() => {
																	crud.rowEdit(scope.row);
																}}>
																{crud.dict.label.update}
															</el-button>
														);
													} else if (vnode === "delete") {
														return (
															<el-button
																size="mini"
																type="text"
																v-show={crud.getPermission(
																	"delete"
																)}
																onClick={() => {
																	crud.rowDelete(scope.row);
																}}>
																{crud.dict.label.delete}
															</el-button>
														);
													} else {
														return renderNode(vnode, {
															scope,
															slots: ctx.$slots
														});
													}
												}
											)}
										</div>
									);
								}
							}
						);
					}
					// 
					else if (["selection", "index"].includes(item.type)) {
						return h(ElTableColumn, item);
					}
					// 
					else {
						const deep = (item: any) => {
							const props = cloneDeep(item);

							// Cannot set property children of #<Element>
							delete props.children;

							return h(ElTableColumn, props, {
								header: (scope: any) => {
									const slot = ctx.$slots[`header-${item.prop}`];

									if (slot) {
										return slot({
											scope
										});
									} else {
										return scope.column.label;
									}
								},
								default: (scope: any) => {
									if (item.children) {
										return <div>{item.children.map(deep)}</div>;
									}

									// Scope data
									const newScope = {
										...scope,
										...item
									};

									// 
									const value = scope.row[item.prop];

									// 
									const slot = ctx.$slots[`column-${item.prop}`];

									if (slot) {
										return slot({
											scope: newScope
										});
									} else {
										// 
										if (item.component) {
											return renderNode(item.component, {
												prop: item.prop,
												scope: newScope.row
											});
										}
										// Formatter
										else if (item.formatter) {
											return item.formatter(
												newScope.row,
												newScope.column,
												newScope.row[item.prop],
												newScope.$index
											);
										}
										// 
										else if (item.dict) {
											const data = item.dict.find(
												(d: any) => d.value == value
											);

											if (data) {
												const ElTag = (
													<el-tag
														disable-transitions
														size="small"
														effect="dark"></el-tag>
												);

												// Use el-tag
												return h(ElTag, data, {
													default() {
														return data.label;
													}
												});
											} else {
												return value;
											}
										}
										// Empty text
										else if (isNull(value)) {
											return scope.emptyText;
										}
										// Value
										else {
											return value;
										}
									}
								}
							});
						};

						return deep(item);
					}
				});
		};

		const isFill = !ctx.columns.find((e: any) => ["selection"].includes(e.type));

		const ElTable = (
			<el-table
				class={[
					"cl-table",
					{
						"is-fill": isFill
					}
				]}
				ref={ctx.setRefs("table")}
				border
				size="mini"
				v-loading={crud.loading}
				data={ctx.data}></el-table>
		);

		return ctx.visible
			? h(
					ElTable,
					{
						onSortChange: ctx.onSortChange,
						maxHeight: ctx.autoHeight ? ctx.maxHeight : null,
						...ctx.props,
						onSelectionChange: ctx.onSelectionChange,
						onRowContextmenu: ctx.onRowContextMenu
					},
					{
						default() {
							return renderColumn();
						},
						empty() {
							return (
								<div class="cl-table__empty">
									{ctx.$slots.empty && ctx.$slots.empty()}
								</div>
							);
						},
						append() {
							return (
								<div class="cl-table__append">
									{ctx.$slots.append && ctx.$slots.append()}
								</div>
							);
						}
					}
			  )
			: null;
	}
});
