import {
	defineComponent,
	getCurrentInstance,
	inject,
	onMounted,
	provide,
	reactive,
	ref
} from "vue";
import { useMitt, useRequest } from "./helper";
import { bootstrap } from "./app";
import Mitt from "../../utils/mitt";
import { deepMerge } from "../../utils";

export default defineComponent({
	name: "cl-crud",

	props: {
		name: String,
		border: Boolean,
		onDelete: Function,
		onRefresh: Function
	},

	emits: ["load"],

	setup(props, { emit }) {
		const ctx = getCurrentInstance();

		// 
		const mitt = new Mitt(ctx?.uid);

		// 
		const crud = reactive<any>({
			dict: {
				api: {
					list: "list",
					add: "add",
					update: "update",
					delete: "delete",
					info: "info",
					page: "page"
				},
				pagination: {
					page: "page",
					size: "size"
				},
				search: {
					keyWord: "keyWord",
					query: "query"
				},
				sort: {
					order: "order",
					prop: "prop"
				},
				label: {
					add: "",
					delete: "",
					multiDelete: "",
					update: "",
					refresh: "",
					advSearch: "",
					saveButtonText: "",
					closeButtonText: ""
				}
			},
			selection: [],
			table: {
				"context-menu": true
			},
			crudRef: ref<any>({}),
			service: {},
			loading: false,
			params: {
				page: 1,
				size: 20
			},
			permission: {
				update: true,
				page: true,
				info: true,
				list: true,
				add: true,
				delete: true
			}
		});

		// 
		Object.assign(crud, useMitt({ mitt }), useRequest({ mitt, props, crud }));

		// 
		provide("crud", crud);
		provide("mitt", mitt);

		onMounted(() => {
			// 
			emit("load", bootstrap(deepMerge(crud, inject("crudConf"))));

			// 
			window.addEventListener("resize", () => {
				mitt.emit("crud.resize");
			});
		});

		return crud;
	},

	render(ctx: any) {
		return (
			<div class={["cl-crud", { "is-border": ctx.border }]} ref="crudRef">
				{ctx.$slots.default && ctx.$slots.default()}
			</div>
		);
	}
});
